<template>
    <div id="app">
        <audio ref="sound1"
               src="https://song.nazvonok.ru/song/7372/mail-agent-message-zvuk-soobscheniya-iz-mayl-agenta.mp3"></audio>
        <audio ref="sound2" src="https://song.nazvonok.ru/song/c92c/masskva-treschotka.mp3"></audio>
        <audio ref="sound3" src="https://song.nazvonok.ru/song/ce6b/zvon-interesnyy-zvon-kotoryy-stavyat.mp3"></audio>
        <audio ref="sound4" src="https://song.nazvonok.ru/song/1969/nota-prosto-notka.mp3"></audio>
        <div>
            <div class="game" :class="{fullSize: player}">
                <div class="square" @click="clicked(1)" :class="{appear: isBlinks[1]}"></div>
                <div class="square" @click="clicked(2)" :class="{appear: isBlinks[2]}"></div>
                <div class="square" @click="clicked(3)" :class="{appear: isBlinks[3]}"></div>
                <div class="square" @click="clicked(4)" :class="{appear: isBlinks[4]}"></div>
                <div class="start" @click="startGame">{{ startButton }}
                    <span v-show="player">{{ showScore }} </span>
                </div>
            </div>
        </div>

        <div v-show="player" class="bottom-bar">
            <p>Уровень сложности: </p><br><br>
            <div><label class="level"><input class="level" type="radio" v-model="speed" value=1500>Легкий</label></div>
            <br>
            <div><label class="level"><input class="level" type="radio" v-model="speed" value=1000>Средний</label></div>
            <br>
            <div><label class="level"><input class="level" type="radio" v-model="speed" value=400>Сложный</label></div>
            <br><br>
            <button :class="{active: !withoutErrors}" @click="withoutErrors = false">С ошибками</button>
            <button :class="{active: withoutErrors}" @click="withoutErrors = true">Без ошибок</button>
        </div>
    </div>

</template>

<script>

    export default {

        data() {
            return {
                startButton: "НАЧАТЬ",/* кнопка старта*/
                player: false,
                isPressed: false,
                isWin: false,/* победа*/
                failure: false,/* неудача*/
                withoutErrors: false,/* для кнопок с ошибками и без ошибок*/
                score: 0,/* счет */
                priority: [],/* очередность ходов*/
                priorityInterval: null,/* интервал очередности*/
                orderPlayerMoves: [],/* чередность ходов игрока*/
                speed: 1500,/* скорость игры*/
                isBlinks: {
                    1: false,
                    2: false,
                    3: false,
                    4: false
                }
            }
        },
        computed: {
            showScore() {           /* выигрыш. спросить игрока будет ли еще играть*/
                if (this.isWin) {
                    return "ЕЩЕ РАЗ?";
                }
                return "Очки: " + this.score;
            }
        },
        watch: {
            strict() {               /* старт игры*/
                this.startGame();
            }
        },
        methods: {

            startGame() {           /* старт игры. начальные условия*/
                this.player = true;
                this.priority = [];
                this.orderPlayerMoves = [];
                this.startButton = "СНАЧАЛА";
                this.isWin = false;
                this.failure = false;
                this.score = 0;
                clearInterval(this.priorityInterval);
                this.showGame();
            },
            clicked(datas) {  /* нажатие на кнопки*/
                if (this.isPressed) {
                    this.playSound(datas);
                    this.BlinksUp(datas);
                    this.orderPlayerMoves.push(datas);
                    this.checkGame();
                }
            },
            checkGame() {  /* проверка не допустил ли игрок ошибку*/
                for (let i = 0; i < this.orderPlayerMoves.length; i++) {
                    if (this.orderPlayerMoves[i] !== this.priority[i]) {
                        if (this.withoutErrors) {
                            this.startButton = "НЕ ПРАВИЛЬНО!";
                            this.failure = true;
                            this.isPressed = false;
                            return setTimeout(() => {
                                this.startGame();
                            }, 1000);
                        }
                        this.orderPlayerMoves = [];
                        this.startButton = "НЕ ПРАВИЛЬНО!";
                        this.failure = true;
                        setTimeout(() => {
                            this.startButton = "СНАЧАЛА";
                            this.failure = false;
                        }, 1000);
                        this.showGame(true);
                    }
                }
                /* если все правильно, совпадают длины массивов игры и игрока продолжаем*/
                if (this.orderPlayerMoves.length === this.priority.length) {
                    this.orderPlayerMoves = [];
                    this.score++;
                    /* победа. дальше не продолжаем. спрашиваем сыграем еще*/
                    if (this.score === 15) {/* очки победы в данном случае 15*/
                        this.startButton = "ПОБЕДА!";
                        this.isPressed = false;
                        this.isWin = true;
                    } else {
                        this.showGame();
                    }
                }
            },
            BlinksUp(datas) {/* мигание кнопок в игре*/
                this.isBlinks[datas] = true;
                setTimeout(() => {
                    this.isBlinks[datas] = false;
                }, 300);
            },
            playSound(datas) {/*звук в игре*/
                if (!this.player)
                    return
                switch (datas) {
                    case 1:
                        this.$refs.sound1.pause()
                        this.$refs.sound1.currentTime = 0
                        this.$refs.sound1.play()
                        this.isBlinks[datas] = true
                        break;
                    case 2:
                        this.$refs.sound2.pause()
                        this.$refs.sound2.currentTime = 0
                        this.$refs.sound2.play()
                        this.isBlinks[datas] = true
                        break;
                    case 3:
                        this.$refs.sound3.pause()
                        this.$refs.sound3.currentTime = 0
                        this.$refs.sound3.play()
                        this.isBlinks[datas] = true
                        break;
                    case 4:
                        this.$refs.sound4.pause()
                        this.$refs.sound4.currentTime = 0
                        this.$refs.sound4.play()
                        this.isBlinks[datas] = true
                        break;
                }
                if (!this.isWin)
                    setTimeout(this.BlinksUp, 750)
            },
            showGame(rewrite) {/* последовательность действий, рандомное нажатие кнопок игрой, скорость игры*/
                let currentIndex = 0;
                this.isPressed = false;
                if (!rewrite) {
                    this.priority.push(Math.floor(Math.random() * 4 + 1));
                }
                this.priorityInterval = setInterval(() => {
                    if (currentIndex >= this.priority.length) {
                        clearInterval(this.priorityInterval);
                        return (this.isPressed = true);
                    }
                    this.playSound(this.priority[currentIndex]);
                    this.BlinksUp(this.priority[currentIndex]);
                    currentIndex++;
                }, this.speed);
            }
        }
    }

</script>

<style lang="scss">
