    $red: #f10909;
    $green: #45AA25;
    $yellow: #f3f306;
    $blue: #2525AA;
    $white: #FFF;

    @mixin center-align {
        align-items: center;
        display: flex;
        justify-content: center;
    }

    body {
        @include center-align();
        background: $main-color;
        font-family: 'Lato', serif;
        font-weight: 900;
        font-size: 140%;
        height: 100vh;
        overflow: hidden;
    }

    .game {
        align-content: space-around;
        background: darken($main-color, 30%);
        border-radius: 3%;
        display: flex;
        flex-wrap: wrap;
        height: 0;
        justify-content: space-around;
        width: 0;

        .start {
            @include center-align();
            align-content: center;
            flex-wrap: wrap;
            background: $white;
            border: 2px solid darken($main-color, 5%);
            cursor: pointer;
            position: absolute;
            height: 100px;
            left: 75%;
            top: 30%;
            user-select: none;
            transform: translate(-50%, -50%);
            width: 200px;

            span {
                font-size: 70%;
            }
        }

        .square {
            cursor: pointer;
            height: 45%;
            width: 45%;

            &:nth-of-type(1) {
                background: $red;

                &.appear {
                    background: lighten($red, 25%);
                }
            }

            &:nth-of-type(2) {
                background: $green;

                &.appear {
                    background: lighten($green, 25%);
                }
            }

            &:nth-of-type(3) {
                background: $yellow;

                &.appear {
                    background: lighten($yellow, 40%);
                }
            }

            &:nth-of-type(4) {
                background: $blue;

                &.appear {
                    background: lighten($blue, 25%);
                }
            }
        }
    }

    .bottom-bar {
        animation: moveup 1s ease-in forwards;
        margin-left: 125%;
        margin-top: -62%;
        width: 100%;
        color: $white;


        button {
            background: darken($main-color, 30%);
            color: $white;
            border-radius: 12px;
            font-size: 100%;
            padding: 2%;
            cursor: pointer;

            &.active {
                background: $green;
            }

            &:nth-of-type(1) {
                margin-right: .3em;
            }
        }

        .level {
            cursor: pointer;
        }

        p {
            color: $white;
            margin: 0;
            font-size: 130%;
            display: inline;
        }
    }

    /* небольшая анимация*/

    .fullSize {
        animation: fullSize ease-out forwards;
    }
    @keyframes fullSize {
        to {
            height: 500px;
            width: 500px;
           }
    }

</style>



